  private davariable22(cad:string,varx:string,op:string):string {
    var re="";
     var otro=cad
     var bb:any=[]
     if(otro.indexOf(varx)>0)
     {
     bb=otro.split(varx)


       if(bb.length==0)
       {
         re="error"
       }
       else{
         var restp=''

        for(let i =0; i <bb.length; i++){
          var cad=''
          cad=bb[i]
          if(cad.indexOf(op)>0)
          {
            var cc:any=bb[i].split(':')
            var dd:any=cc[1]

            var ee:any=dd.split('"')
            if(dd=='{}')
            {
              restp=dd;
            }
            else
            {
              restp=ee[1];
              if(op=='CICLO_ACTIVO')
              {
                break;
                 // break;

              }

            }


            if(op=='CICLO_ACTIVO')
            {

               // break;

            }
            else{
              break;
            }

          }
        }

        re=restp;


       }
      }
      else
      {
        re="error"
      }

     return re;
   }
  private davariable(cad:string,varx:string):string {
    var re="";
     var otro=cad
     var bb:any=[]
     if(otro.indexOf(varx)>0)
     {
     bb=otro.split(varx)


       if(bb.length==0)
       {
         re="error"
       }
       else{

         var cc:any=bb[1].split(';')
         var dd:any=cc[1]
         var ee:any=dd.split('&')
         re=ee[0];

       }
      }
      else
      {
        re="error"
      }

     return re;
   }
